import React, { Component } from 'react';
import { Grid, Avatar, Card, CardHeader, Typography, Paper } from '@material-ui/core';
import indianFlag from '../../assets/ind.png';
import axios from 'axios';
import CountUp from 'react-countup';
import classes from './India.module.css';

class India extends Component {
    state = {
        statewise: []
    }
    componentDidMount(){
        axios.get('https://api.covid19india.org/data.json')
            .then(response => {
                const country = response.data.statewise.slice(0,1);
                this.setState({statewise: country});
            //    console.log(country);
            });
    }
    render(){
        const Confirmed = this.state.statewise.map(infos => {
        //    console.log(infos);
            return <Paper key={infos.state} variant="outlined" className={classes.mainCard}>
                        <Typography className={classes.title}>Confirmed</Typography>
                        <Typography style={{color: '#e53935', fontSize: '25px', fontWeight: 'bold'}}>
                            <CountUp start={0} end={infos.confirmed} duration={2.75} separator="," />
                        </Typography>
                        <Typography style={{color: '#ef5350', fontSize: '12px', fontWeight: 'bold'}}>Today +
                            <CountUp start={0} end={infos.deltaconfirmed} duration={2.75} separator="," />
                        </Typography>
                    </Paper>
        });
        const Recovered = this.state.statewise.map(infos => {
        //    console.log(infos);
            return <Paper key={infos.state} variant="outlined" className={classes.mainCard}>
                        <Typography className={classes.title}>Recovered</Typography>
                        <Typography style={{color: '#43a047',fontSize: '25px', fontWeight: 'bold'}}>
                            <CountUp start={0} end={infos.recovered} duration={2.75} separator="," />
                        </Typography>
                        <Typography style={{color: '#66bb6a',fontSize: '12px', fontWeight: 'bold'}}>Today +
                            <CountUp start={0} end={infos.deltarecovered} duration={2.75} separator="," />
                        </Typography>
                    </Paper>
        });
        const Deaths = this.state.statewise.map(infos => {
        //    console.log(infos);
            return <Paper key={infos.state} variant="outlined" className={classes.mainCard}>
                        <Typography className={classes.title}>Deaths</Typography>
                        <Typography style={{color: '#757575',fontSize: '25px', fontWeight: 'bold'}}>
                            <CountUp start={0} end={infos.deaths} duration={2.75} separator="," />
                        </Typography>
                        <Typography style={{color: '#bdbdbd',fontSize: '12px', fontWeight: 'bold'}}>Today +
                            <CountUp start={0} end={infos.deltadeaths} duration={2.75} separator="," />
                        </Typography>
                    </Paper>
        });
        const Active = this.state.statewise.map(infos => {
        //    console.log(infos);
            return <Paper key={infos.state} variant="outlined" className={classes.mainCard}>
                        <Typography className={classes.title}>Active</Typography>
                        <Typography style={{color: '#c0ca33',fontSize: '25px', fontWeight: 'bold'}}>
                            <CountUp start={0} end={infos.active} duration={2.75} separator="," />
                        </Typography>
                        <Typography style={{color: '#bdbdbd',fontSize: '12px', fontWeight: 'bold'}}>{infos.lastupdatedtime}</Typography>
                    </Paper>
        });
        return(
            <div className="India">
                <Grid container spacing={3}>
                    <Grid item xs={12} align='center'>
                        <Card variant="outlined">
                            <CardHeader 
                                avatar={
                                    <Avatar src={indianFlag} variant="square" />
                                }
                                title={
                                    <Typography variant="h5">
                                        INDIA
                                    </Typography>
                                }
                                subheader="Covid-19 Tracker"
                            />
                        </Card>
                    </Grid>
                </Grid>

                <Grid container spacing={3} className={classes.mainCards}>
                    <Grid item xs={6} md={3}  align='center'>
                        {Confirmed}
                    </Grid>
                    <Grid item xs={6} md={3}  align='center'>
                        {Recovered}
                    </Grid>
                    <Grid item xs={6} md={3}  align='center'>
                        {Deaths}
                    </Grid>
                    <Grid item xs={6} md={3}  align='center'>
                        {Active}
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default India;