import React, { Component } from "react";
import { Grid, Card, CardHeader, Avatar, Typography, CardContent, Paper} from '@material-ui/core';
import { Doughnut, HorizontalBar } from 'react-chartjs-2';
import axios from 'axios';
import CountUp from 'react-countup';
import classes from './State.module.css';

class States extends Component {
    state={
        states: [],
        dist: '',
    }
    componentDidMount(){
        axios.get('https://api.covid19india.org/data.json')
            .then(response => {
                const specfState = response.data.statewise.slice(1,37);
                this.setState({states: specfState});
             //   console.log(specfState);
            });
    }
    render(){
        const preticularState = this.state.states.map(details => {
            return(
                <Grid container spacing={3}>
                <Grid item xs={12} md={12}>
                <Card variant="outlined">
                    <CardHeader
                        avatar={
                            <Avatar style={{background: '#1e88e5', fontWeight: 'bold'}}>
                                {details.statecode}
                            </Avatar>
                        }
                        title={
                            <Typography>
                                {details.state}
                            </Typography>
                        }
                        subheader={
                            <Typography style={{color: '#bdbdbd',fontSize: '12px'}}>
                                {details.lastupdatedtime}
                            </Typography>
                        }
                    />
                    <CardContent align="center">
                        <Grid container spacing={3}>
                        <Grid item xs={6} md={3}>
                                <Paper elevation={0}>
                                    <Typography className={classes.title} style={{fontSize: '14px'}}>Confirmed</Typography>
                                    <Typography style={{color: '#e53935', fontSize: '20px', fontWeight: 'bold'}}>
                                        <CountUp start={0} end={details.confirmed} duration={2.75} separator="," />
                                    </Typography>
                                    <Typography style={{color: '#ef5350', fontSize: '16px', fontWeight: 'bold'}}>+
                                        <CountUp start={0} end={details.deltaconfirmed} duration={2.75} separator="," />
                                    </Typography>
                                </Paper>
                            </Grid>
                        <Grid item xs={6} md={3}>
                                <Paper elevation={0}>
                                    <Typography className={classes.title} style={{fontSize: '14px'}}>Recovered</Typography>
                                    <Typography style={{color: '#43a047',fontSize: '20px', fontWeight: 'bold'}}>
                                        <CountUp start={0} end={details.recovered} duration={2.75} separator="," />
                                    </Typography>
                                    <Typography style={{color: '#66bb6a',fontSize: '16px', fontWeight: 'bold'}}>+
                                        <CountUp start={0} end={details.deltarecovered} duration={2.75} separator="," />
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={6} md={3}>
                                <Paper elevation={0}>
                                    <Typography className={classes.title} style={{fontSize: '14px'}}>Deaths</Typography>
                                    <Typography style={{color: '#757575',fontSize: '20px', fontWeight: 'bold'}}>
                                        <CountUp start={0} end={details.deaths} duration={2.75} separator="," />
                                    </Typography>
                                    <Typography style={{color: '#bdbdbd',fontSize: '16px', fontWeight: 'bold'}}>+
                                        <CountUp start={0} end={details.deltadeaths} duration={2.75} separator="," />
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={6} md={3}>
                                <Paper elevation={0}>
                                    <Typography className={classes.title} style={{fontSize: '14px'}}>Active</Typography>
                                    <Typography style={{color: '#c0ca33',fontSize: '20px', fontWeight: 'bold'}}>
                                        <CountUp start={0} end={details.active} duration={2.75} separator="," />
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <Paper elevation={0}>
                                    <Doughnut
                                        data={{
                                            labels:['Confirmed', 'Recovered', 'Deaths', 'Acitve'],
                                            datasets:[
                                                {
                                                    backgroundColor: ['#0288d1', '#43a047', '#e53935','#c0ca33'],
                                                    data: [details.confirmed, details.recovered, details.deaths, details.active],
                                                }
                                            ]
                                        }}
                                        options={{ legend: { display: true, position: 'bottom' }, }}
                                    />
                                </Paper>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <Paper elevation={0}>
                                    <HorizontalBar
                                        data={{
                                            labels:['Confirmed', 'Recovered', 'Deaths', 'Acitve'],
                                            datasets:[
                                                {
                                                    backgroundColor: ['#0288d1', '#43a047', '#e53935','#c0ca33'],
                                                    data: [details.confirmed, details.recovered, details.deaths, details.active],
                                                }
                                            ]
                                        }}
                                        options={{ legend: { display: false, position: 'bottom' }, }}
                                    />
                                </Paper>
                            </Grid>
                        </Grid>
                        <Grid item xs={12}>
                        </Grid>
                    </CardContent>
                </Card>
                </Grid>
                </Grid>
            );
        });
        return(
            <div>
                <Grid container spacing={10}>
                    <Grid item xs={12} md={12}>
                            {preticularState}
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default States;