import React, { Component } from 'react';
import {Grid, Paper} from '@material-ui/core';
import { Bar, HorizontalBar, Pie } from 'react-chartjs-2';
import axios from 'axios';

class Chart extends Component{
    state = {
        ind: []
    }
    componentDidMount(){
        axios.get('https://api.covid19india.org/data.json')
            .then(res => {
                const Ind = res.data.statewise.slice(0,1);
                this.setState({ind: Ind})
            //    console.log(Ind, "Charts");
            });
    }
    render(){
        const barChart = this.state.ind.map(info => {
            return(
                    <Bar
                        data={{
                            labels:['Confirmed', 'Recovered', 'Deaths', 'Acitve'],
                            datasets:[
                                {
                                    backgroundColor: ['#0288d1', '#43a047', '#e53935','#c0ca33'],
                                    data: [info.confirmed, info.recovered, info.deaths, info.active],
                                }
                            ]
                        }}
                        options={{ legend: { display: false }, }}
                    />
            );
        });
        const HbarChart = this.state.ind.map(info => {
            return(
                    <HorizontalBar
                        data={{
                            labels:['Confirmed', 'Recovered', 'Deaths', 'Acitve'],
                            datasets:[
                                {
                                    backgroundColor: ['#0288d1', '#43a047', '#e53935','#c0ca33'],
                                    data: [info.confirmed, info.recovered, info.deaths, info.active],
                                }
                            ]
                        }}
                        options={{ legend: { display: false }, }}
                    />
            );
        });
        const pieChart = this.state.ind.map(info => {
            return(
                    <Pie
                        data={{
                            labels:['Confirmed', 'Recovered', 'Deaths', 'Acitve'],
                            datasets:[
                                {
                                    backgroundColor: ['#0288d1', '#43a047', '#e53935','#c0ca33'],
                                    data: [info.confirmed, info.recovered, info.deaths, info.active],
                                }
                            ]
                        }}
                        options={{ legend: { display: true }, }}
                    />
            );
        });
        return(
            <div>
                <Grid container spacing={3}>
                    <Grid item xs={12} md={4}  align='center'>
                        <Paper variant="outlined">
                            {HbarChart}
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={4}  align='center'>
                        <Paper variant="outlined">
                            {pieChart}
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={4}  align='center'>
                        <Paper variant="outlined">
                            {barChart}
                        </Paper>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default Chart;